import React from 'react';
import { ListGroupItem, Media } from 'react-bootstrap';

const VideoListItem = ({ video, onSelectFromVideoList }) => {
    const thumbnailUrl = video.snippet.thumbnails.default.url;
    return (
        <ListGroupItem onClick={() => onSelectFromVideoList(video)}>
            <Media>
                <Media.Left>
                    <img src={thumbnailUrl} alt='thumbnail' />
                </Media.Left>
                <Media.Body>
                    <Media.Heading>
                        {video.snippet.title.toLowerCase()}
                    </Media.Heading>
                </Media.Body>
            </Media>
        </ListGroupItem>
    );
};

export default VideoListItem;


