import React, { Component } from 'react';
import _ from 'lodash';
import YTSearch from 'youtube-api-search';
import SearchBar from './search_bar';
import VideoList from './video_list';
import VideoDetail from './video_detail';

const API_KEY = 'AIzaSyCP0CjM_kYDj63DT1a14mcvCR4P--VMjpw';

class App extends Component {
    constructor(props) {
        super(props);
                
        this.state = {
            videos: [],
            selectedVideo: null
        };

        this._videoSearch('');
    }

    _videoSearch(searchTerm) {
        YTSearch(
            {
                key: API_KEY,
                term: searchTerm
            },
            (fetchedData) => {
                this.setState(
                    {
                        videos: fetchedData,
                        selectedVideo: fetchedData[0]
                    }
                );
            }
        );
    }

    _videoSelectHandler = (selectedVideo) => {
        this.setState({selectedVideo});
    };

    render() {
        const throttledSearch = _.debounce((term) => {this._videoSearch(term)}, 500);
        return (
            <div>
                <SearchBar onSearchTermChange={throttledSearch} />
                <VideoDetail video={this.state.selectedVideo} />
                <VideoList
                    videos={this.state.videos}
                    onVideoSelect={this._videoSelectHandler}
                />
            </div>
        );
    }
}

export default App;


